*, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%;

    @include respond(deskBig) {
        font-size: 75%;
    }
    
    @include respond(tabLand) {
        font-size: 56.25%;
    }
    
    @include respond(tabPort) {
        font-size: 50%;
    }
}

body {
    padding: 3rem;
    box-sizing: border-box;
    
    @include respond(tabPort) {
        padding: 0;
    }
}


::selection {
    background-color: lighten($color-primary, .1);
    color: $color-white;
}
