.composition {
    position: relative;

    &__photo {
        width: 55%;
        box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
        position: absolute;
        z-index: 10;
        transition: all .2s;
        outline-offset: 1rem;
    
        @include respond(tabPort) {
            position: relative;
            float: left;
            width: 33.3333333%;
            box-shadow: 0 1.5rem 3rem rgba($color-black, .2);
        }
    
        &--p1 {
            left: 0;
            top: -2rem;
            
            @include respond(tabPort) {
                transform: scale(1.2);
                top: 0;
            }
        }

        &--p2 {
            right: 0;
            top: 2rem;
            
            @include respond(tabPort) {
                transform: scale(1.3);
                top: -1rem;
                z-index: 100;
            }
        }

        &--p3 {
            left: 20%;
            top: 11rem;
            
            @include respond(tabPort) {
                top: 1rem;
                left: 0;                
                transform: scale(1.1);
            }
        }

        &:hover {
            outline: 1rem solid $color-primary;
            transform: scale(1.05) translateY(-.5rem);
            box-shadow: 0 2.5rem 4rem rgba($color-black, .5);
            z-index: 20;
        }
    }

    &:hover &__photo:not(:hover) {
        transform: scale(.95);
    }
}