.form {
    &__group:not(:last-child) {
        margin-bottom: 2rem;
    }

    &__input { //doesn't inherit font properties
        font-size: 1.5rem;
        font-family: inherit;
        color: inherit;
        padding: 1.5rem 2rem;
        border-radius: 2px;
        background-color: rgba($color-white, .5);
        border: none;
        border-bottom: 3px solid transparent;
        width: 90%;
        display: block;
        transition: all .2s;
        
        @include respond(tabPort) {
            width: 100%;
        }
        
        &:focus {
            outline: none;
            box-shadow: 0 1rem 2rem rgba($color-black, .1);
            border-bottom: 3px solid $color-primary;
            
            &:invalid {
                border-bottom: 3px solid $color-secondary-dark;
            }
        }
        
        &::-webkit-input-placeholder {
            color: $color-grey-dark-2;
        }
    }

    &__label {
        font-size: 1.2rem;
        font-weight: 700;
        margin-left: 2rem;
        margin-top: .7rem;
        display: block;
        transition: all .3s;
    }

    &__input:placeholder-shown + &__label { //adjacent sibling
        opacity: 0;
        visibility: hidden;
        transform: translateY(-4rem);
    }

    &__radio-input {
        display: none;
    }
    
    &__radio-group {
        display: inline-block;
        width: 49%;

        @include respond(tabPort) {
            width: 100%;
            margin-bottom: 2rem;
        }
    }

    &__radio-label {
        font-size: $default-font-size;
        cursor: pointer;
        position: relative;
        padding-left: 4.5rem;
    }

    &__radio-button {
        height: 3rem;
        width: 3rem;
        border: 5px solid $color-primary;
        border-radius: 50%;
        display: inline-block; //NECESSARY
        position: absolute;
        left: 0;
        top: -.5rem;

        &::after {
            content: "";
            display: block;
            height: 1.3rem;
            width: 1.3rem;
            border-radius: 50%;    
            @include absCenter;
            background-color: $color-primary;
            opacity: 0;
        }
    }

    &__radio-input:checked ~ &__radio-label &__radio-button::after { //direct-descendant
        opacity: 1;
    }
}