.footer {
    background-color: $color-grey-dark-3;
    padding: 10rem 5rem;
    font-size: 1.4rem;
    color: $color-grey-light-1;

    @include respond(tabPort) {
        padding: 8rem 0;
    }

    &__logo-box {
        text-align: center;
        margin-bottom: 8rem;
    
        @include respond(tabPort) {
            margin-bottom: 4rem;
        }
        
        .footer__logo {
            width: 25%;
            height: auto;
        }
    }

    &__navigation {
        border-top: 1px solid $color-grey-light-2;
        padding-top: 2rem;
        display: inline-block;

        @include respond(tabPort) {
            width: 90%;
            text-align: center
        }
    }

    &__item {
        display: inline-block;

        &:not(:last-child) {
            margin-right: 1.5rem;
        }
    }

    &__link {
        transition: all .2s;

        &:link, &:visited {
            color: $color-grey-light-1;
            background-color: $color-grey-dark-3;
            text-decoration: none;
            text-transform: uppercase;
            display: inline-block;
        }

        &:hover, &:active {
            color: $color-primary;
            box-shadow: 0 1rem 2rem rgba($color-black, .4);
            transform: rotate(5deg) scale(1.3);
        }
    }

    &__copyright {
        border-top: 1px solid $color-grey-light-2;
        padding-top: 2rem;
        width: 80%;
        float: right;

       @include respond(tabPort) {
            width: 90%;
            float: none;
        }
    }
}
